<h1 id="数据库事务" tabindex="-1"><a class="header-anchor" href="#数据库事务" aria-hidden="true">#</a> 数据库事务</h1>
<p>数据库事务由严格的定义，它必须满足4个特性：</p>
<p>原子性(Atomicity),一致性(consistency),隔离性(Isolation),持久性(Durability)</p>
<p>ACID</p>
<h2 id="原子性-atomic" tabindex="-1"><a class="header-anchor" href="#原子性-atomic" aria-hidden="true">#</a> 原子性(Atomic)</h2>
<p>只有所有的操作执行成功，整个事务才提交。</p>
<h2 id="一致性-consistency" tabindex="-1"><a class="header-anchor" href="#一致性-consistency" aria-hidden="true">#</a> 一致性(Consistency)</h2>
<h2 id="隔离性-isolation" tabindex="-1"><a class="header-anchor" href="#隔离性-isolation" aria-hidden="true">#</a> 隔离性(Isolation)</h2>
<h2 id="持久性-durabiliy" tabindex="-1"><a class="header-anchor" href="#持久性-durabiliy" aria-hidden="true">#</a> 持久性(Durabiliy)</h2>
